.loading{
  display: block;
}

.overlay {
  position: fixed;
  justify-content: center;
  align-items: center;
  display: flex;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5);
  z-index: 2;
  cursor: pointer;
}

.preloader {
  background-image: url(../../assets/images/loading.gif);
  z-index: 4;
  background-size: cover;
  width: 250px;
  height: 200px;
}